#start-text {
    font-size: 36px;
    word-spacing: 0.25em;
    font-family: AmericanCaptain;
    text-align: center;
    color: white;
    background: rgba(0, 0, 0, 0.8);
    vertical-align: middle;
    align-items: center;
    align-content: center;
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    margin-top: 80%;
}

@font-face {
    font-family: "AmericanCaptain";
    src: url("../common/assets/american_captain.ttf");
}

#idle-video {
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

#win-video {
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

#lose-video {
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

progress[value] {
    /*border-radius: 2px; */
    background-size: 35px 20px, 100% 100%, 100% 100%;
    color: red;
    background-color: black;
}

progress[value]::-webkit-progress-value {
    background-color: red;
}

#progress-bar {
    align-items: center;
    align-content: center;
    width: 100%;
}

#question-text {
    font-size: 40px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    color: white;
    background: rgba(0, 0, 0, 0.8);
    vertical-align: middle;
    overflow: hidden;
    align-items: center;
    align-content: center;
    width: 100%;
    height: 97%;
    display: flex;
    justify-content: center;
}